* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

@keyframes drift {
  0%, 100% {
    transform: translateX(2.2%)translateY(-5%);
  }

  50% {
    transform: translateX(-6.1%)translateY(17.5%);
  }
}

@keyframes slideRight {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-10%);
  }
}

@keyframes slideLeft {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(10%);
  }
}

