* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-10px);
    }
}

@keyframes drift {
    0%, 100% {
        transform: translateX(2.2%) translateY(-5%);
    }
    50% {
        transform: translateX(-6.1%) translateY(17.5%);
    }
}

@keyframes slideRight {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-10%);
    }
}

@keyframes slideLeft {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(10%);
    }
}


body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

.static-bg {
    font-size: 12px;
    font-family: sans-serif;
    --token-cf50e1e4-7f55-4aa2-9af8-f61c3d25e531: rgb(243, 245, 151);
    --token-7d2312d1-edd7-4229-828d-c62ca2e9628d: rgb(89, 252, 143);
    --framer-aspect-ratio-supported: auto;
    --framer-will-change-override: none;
    pointer-events: none;
    box-sizing: border-box;
    -webkit-font-smoothing: inherit;
    background: url("https://framerusercontent.com/images/rR6HYXBrMmX4cRpXfXUOvpB0.png");
    opacity: 0.2;
    inset: -200%;
    width: 400%;
    height: 400%;
    position: absolute;
    will-change: transform;
    transform: translateX(2.2%) translateY(-5%);
    z-index: -3;
}

.parallax-section {
    position: relative;
    height: 100vh;
    overflow: hidden;
    border: 3px solid black;
    box-sizing: border-box;
}

.parallax-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    will-change: transform;
}

.blender-section {
    position: relative;
    height: 100vh;
    overflow: hidden;
    border: 3px solid black;
    box-sizing: border-box;
}

.section-divider {
    height: 5px;
    background: black;
    margin: 0;
}

.bg1.layer1 {
    background-color: #f0f0f0;
    z-index: -2;
}

.bg1.layer2 {
    background-color: #e0e0e0;
    z-index: -1;
}

.bg2.layer1 {
    background-color: #d0d0d0;
    z-index: -2;
}

.bg2.layer2 {
    background-color: #c0c0c0;
    z-index: -1;
}

.bg3.layer1 {
    background-color: #b0b0b0;
    z-index: -2;
}

.bg3.layer2 {
    background-color: #a0a0a0;
    z-index: -1;
}

.bg4.layer1 {
    background-color: #909090;
    z-index: -2;
}

.bg4.layer2 {
    background-color: #808080;
    z-index: -1;
}

.content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
    color: black;
    padding: 0 2rem;
}

.content.front {
    z-index: 20;
}

.content.back {
    z-index: 5;
}

.text-layer-1 {
    font-size: 6vw;
    font-weight: 400;
    font-family: 'Special Gothic Expanded One', sans-serif;
    text-align: center;
    position: absolute;
    width: 100%;
    left: 0;
    margin: 0;
    z-index: 20;
}

@media (max-width: 768px) {
    .text-layer-1 {
        font-size: 8vw;
    }
}

.text-layer-1 span {
    display: inline-block;
    animation: float 3s ease-in-out infinite;
}

.text {
    white-space: nowrap;
    animation: slideRight 15s linear infinite;
}

.text-layer-2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 30;
    font-size: 4vw;
    font-weight: 400;
    font-family: 'Special Gothic Expanded One', sans-serif;
    text-align: center;
    white-space: nowrap;
    animation: bounceIn 2s ease-out forwards;
    opacity: 0;
}

@media (max-width: 768px) {
    .text-layer-2 {
        font-size: 6vw;
    }
}

.text-layer-2 span {
    display: inline-block;
    animation: float 3s ease-in-out infinite;
}

@keyframes easeIn {
    0% {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.5);
    }
    100% {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

@keyframes bounceIn {
    0% {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0);
    }
    50% {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1.2);
    }
    70% {
        transform: translate(-50%, -50%) scale(0.9);
    }
    100% {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

